<template>
  <div class="container app-view">

    <ul class="tab tab-block">
      <li v-for="(tab, tabId) in tabs" class="tab-item" :key="tabId">
        <a class="c-hand"
          :class="{active: tabId === currentTab, badge: tab.badge}"
          @click="currentTab = tabId"
          :data-badge="tab.badge"
        >{{tab.name}}</a>
      </li>
    </ul>

    <div class="app-body">

      <ul class="menu app-menu">
        <li class="menu-item">
          <div class="tile tile-centered">
            <div class="tile-icon"><img class="avatar" src="static/img/icons/store.svg"></div>
            <div class="tile-content">App Manager</div>
          </div>
        </li>
        <li class="divider"> </li>
        <li v-for="(tab, tabId) in tabs" class="menu-item" :key="tabId">
          <a class="c-hand"
            :class="{active: tabId === currentTab, badge: tab.badge}"
            @click="currentTab = tabId"
            :data-badge="tab.badge">
            <!-- <i class="icon icon-link"></i>  -->
            {{tab.name}}
          </a>
        </li>
      </ul>

      <div class="app-content">
        <!-- Installed -->
        <div class="card app-list" v-show="currentTab === 'installed'">
          <div class="card-header">

            <div class="has-icon-right float-right" >
              <input type="text" class="form-input" placeholder="Search" v-model="installedAppFilter">
              <i class="form-icon icon icon-cross  c-hand" v-if="installedAppFilter !== ''" @click="installedAppFilter = ''" ></i>
              <i class="form-icon icon icon-search" v-if="installedAppFilter === ''" ></i>
            </div>
            <span class="loading-lg loading-icon float-right" :class="{loading: isLoading}"></span>

            <span class="card-title h5">Installed Apps </span>

          </div>

          <div class="card-body app-list-body">
            <div class="card app-item" v-for="app in installedApps" :key="app">
              <div class="card-body">
                {{app}}
              </div>
              <div class="card-footer">
                <button class="btn btn-primary float-right">Open</button>
                <button class="btn btn-error float-right">Uninstall</button>
                <button class="btn btn-success float-right">Update</button>
              </div>
            </div>

          </div>
        </div>

        <!-- Available -->
        <div class="card" v-show="currentTab === 'available'">
          <div class="card-header">

          </div>
          <div class="card-body">
            <form class="form-horizontal">
              <div class="form-group">
                <div class="col-4 col-sm-12">
                  <label class="form-label">Omega Name</label>
                </div>
                <div class="col-8 col-sm-12">
                  <input class="form-input" type="text">
                </div>
              </div>
              <div class="form-group">
                <div class="col-4 col-sm-12">
                  <label class="form-label">Timezone</label>
                </div>
                <div class="col-8 col-sm-12">
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
export default {
  name: 'AppManagerApp',
  data () {
    return {
      currentTab: 'installed',
      tabs: {
        installed: {name: 'Installed'},
        available: {name: 'Available'}
      },
      isLoading: true,
      installedApps: [],
      installedAppFilter: ''
    }
  },
  computed: {

  },
  props: {
  },
  methods: {

  },
  created: function () {
    // fetch the wifi config data
    this.$store.dispatch('request', {
      object: 'onion',
      method: 'wifi-setup',
      params: {
        command: 'list',
        params: {}
      }
    })

    this.$store.dispatch('handleCommand', {cmd: 'opkg', params: ['list-installed']})
      .then(resp => {
        // dispatch('notifyApp', {appUid: data.instance, eventId: eventId, data: {cmd: data.content.cmd, resp: resp.stdout}})
        for (var pkg of resp.stdout.split('\n')) {
          if (pkg.indexOf('oos-app') > -1) {
            var pkgId = pkg.split(' - ')[0]
            var pkgVersion = pkg.split(' - ')[1]
            this.installedApps.push(pkgId + ` (${pkgVersion})`)
          }
        }
        this.isLoading = false
      })

    //   .then(resp => {
    //     if (resp.success) {
    //       this.wifi.configured = resp.results
    //     }
    //   })
    // // start scanning for wifi networks
    // this.$store.dispatch('request', {
    //   object: 'onion',
    //   method: 'wifi-scan',
    //   params: {
    //     device: this.wifi.radio
    //   }
    // })
    //   .then(resp => {
    //     console.log(resp)
    //     if (resp.results && resp.results instanceof Array) {
    //       this.wifi.scanned = resp.results
    //     }
    //   })
  }
}
</script>

<style scoped>
.btn {
    margin-left: 10px;
}
.avatar {
  background: inherit;
}
.app-list {
  text-align: left;
  width: 100vw;
  max-width: 800px;
  margin: auto;
  margin-top: 20px;
}
.app-view {
  padding: 20px;
  background-color: #f8f9fa;
  height: 100%;
  overflow: auto;
}
.app-menu {
  margin: 16px;
  margin-right: 40px;
  z-index: 10;
}
.app-body {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.app-list-body {
  display: flex;
  /* flex-direction: */
  flex-wrap: wrap;
  align-content: space-evenly;
  justify-content: space-between;
}

.app-item {
  width: 100%;
  max-width: 360px;
  margin: 5px;
}

.tab {
  display: none;
}

.loading-icon {
  margin-right: 30px;
}
@media only screen and (max-width: 1050px)  {
  .tab {
    display: flex;
  }

  .app-menu {
    display: none;
  }
}

@media only screen and (max-width: 774px)  {
  .app-list-body {
    justify-content: center;
  }
}
</style>



